<script>
// @ is an alias to /src
import Navbar from '@/components/Navbar.vue'
import Breadcrumb from '@/components/Breadcrumb.vue'
import CTA from '@/components/CTA.vue'
import TestimonialTwo from '@/components/TestimonialTwo.vue'

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation, Pagination, A11y, Autoplay } from "swiper";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/autoplay";

import axios from 'axios'

export default {
    name: 'Service',
    components : {Navbar,  Breadcrumb, Swiper, SwiperSlide,CTA,TestimonialTwo},
    data(){
        return {
			prices : [],
			services : [],
            images : [],

            modules: [Navigation, Pagination, A11y, Autoplay],
            swiperOptions: {
                breakpoints: {
                354: {
                    slidesPerView: 1,
                    spaceBetween: 10,
                },
                575: {
                    slidesPerView: 1,
                    spaceBetween: 10,
                },
                767: {
                    slidesPerView: 2,
                    spaceBetween: 50,
                },

                990: {
                    slidesPerView: 3,
                    spaceBetween: 30,
                },
                1200: {
                    slidesPerView: 4,
                    spaceBetween: 30,
                },
                },
            },
        }
    },

	
    async beforeMount(){
        let fetchedData = await axios.get('/assets/data/services.json');
        this.services = fetchedData.data.services;

		let fetchePricedData = await axios.get("/assets/data/prices.json");
        this.prices = fetchePricedData.data.prices;
		
    },

	mounted(){
        //scrolling to top of the window
        window.scrollTo(0, 0);		
	}

}
